	import { doLogin, getLoginCode, getUserInfo } from "@/common/api/user"
	import { checkPhone, checkPwd } from "@/common/common.js"
    import { mapState, mapMutations } from 'vuex'
	
	export default {
		data() {
			return {
				name: '',
				account: '',
				password: '',
				openId: '',
				positionTop: 0,
				askLoginCode: true,
				counter: 60,
                platform: 'manage_wx_app'
			}
		},
		onLoad:function(e){
			console.log(e);
			this.openId = e.openId
			// this.openId = e.name
		},
		onReady() {
            this.initPosition();
        },
		methods: {
			initPosition() {
                /**
                 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
                 * 反向使用 top 进行定位，可以避免此问题。
                 */
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
            isHidePasswordClick(){
				this.isHidePassword=!this.isHidePassword
			},
			handleLoginCode() {
				if(!checkPhone(this.account)){
					return;
				};
				getLoginCode(this.account).then((res)=>{
					uni.showToast({
						title: '验证码已发送',
						icon: 'success',
						mask: true
					});
					this.askLoginCode = false
					this.timedown()
				}).catch((err)=>{
					const msg = err.msg;
					uni.showToast({
						title: msg,
						mask: true,
						icon: 'none'
					});
				})
			},
			bindUser() {
				if(!checkPhone(this.account)){
					return;
				};
				if(!checkPwd(this.password)){
					return;
				};
				/*
				* 登录逻辑
				*/
				doLogin({ username: this.account, password: this.password, openId: this.openId, platform: this.platform }).then((res)=>{
					this.loading = false;
					console.log('request success', res)
					uni.showToast({
						title: '请求成功',
						icon: 'success',
						mask: true
					});
                    this.login(res.data)
                    getUserInfo().then((res) => {
                        this.info(res.data)
                        this.toMain()
                    })
					
				}).catch((err)=>{
					this.loading = false;
					const msg = err.msg;
					uni.showToast({
						title: msg,
						mask: true,
						icon: 'none'
					});
					console.log('request fail', err);
				})
			},
			timedown() {
				const that = this;
				if (this.counter > 0) {
					this.counter = this.counter - 1;
					setTimeout(function() {
						that.timedown()
					}, 1000); 
				}
				else {
					this.askLoginCode = true;
					this.counter = 60
				}
			},
			toMain() {
                
                uni.reLaunch({
					url: '../main/main',
				});
            },
			...mapMutations(['login', 'info'])
		}
	}
